import {GameStateType} from '../game-type';
import {getPlayerHeroList} from '../player/player-helper';

import {GameTriggerConditionEnum, GameTriggerType, TriggerCallbackType} from './trigger-type';

export const triggerConditionMap: Record<GameTriggerConditionEnum, TriggerCallbackType> = {
    [GameTriggerConditionEnum.allHeroesDied]: (trigger: GameTriggerType, gameState: GameStateType): boolean => {
        return getPlayerHeroList(trigger.forActivePlayerId, gameState.unitList).length === 0;
    },
    [GameTriggerConditionEnum.oneHeroLivesOnly]: (trigger: GameTriggerType, gameState: GameStateType): boolean => {
        return getPlayerHeroList(trigger.forActivePlayerId, gameState.unitList).length === 1;
    },
    [GameTriggerConditionEnum.unitOnPlace]: (trigger: GameTriggerType, gameState: GameStateType): boolean => {
        console.log(gameState);
        return false;
    },
} as const;
