import {GamePopup} from '../../../../layout/game-popup/game-popup';
import gamePopupStyle from '../../../../layout/game-popup/game-popup.scss';
import {Locale} from '../../../../provider/locale/locale-context';
import {UiButton} from '../../../../layout/ui-button/ui-button';

import {ClientGameMenu} from './client-game-menu/client-game-menu';

type ClientGameMenuPopupType = {
    isOpen: boolean;
    setIsGameMenuOpen: (isGameMenuOpen: boolean) => void;
    setIsGameStarMessageOpen: (isShow: boolean) => void;
};

export function ClientGameMenuPopup(props: ClientGameMenuPopupType): JSX.Element {
    const {setIsGameStarMessageOpen, isOpen, setIsGameMenuOpen} = props;

    return (
        <GamePopup isOpen={isOpen}>
            <h3 className={gamePopupStyle.game_popup__header}>
                <Locale stringKey="BATTLE_MENU__MENU" />
            </h3>
            <ClientGameMenu setIsGameStarMessageOpen={setIsGameStarMessageOpen} />
            <div className={gamePopupStyle.game_popup__footer}>
                <UiButton
                    isTextToCenter
                    langKey="BATTLE_MENU__RETURN_TO_GAME"
                    onClick={() => setIsGameMenuOpen(false)}
                />
            </div>
        </GamePopup>
    );
}
