import {useEffect, useState, MutableRefObject, useCallback} from 'react';
import {SpringRef, SpringValues} from '@react-spring/web';

import {useGameContext} from '../../../../provider/game/game-context';
import {getPlayerByPlayerId} from '../../../../model/game/player/player-helper';
import {GamePlayerTypeEnum} from '../../../../model/game/player/player-type';
import {getPointActionList} from '../../../../model/game/bot/bot-helper';
import {GameActionTypeEnum, MovedGameActionType} from '../../../../provider/game/game-context-type';
import {defaultGameAction} from '../../../../provider/game/game-context-const';
import {getMovePathFromToByPathList} from '../../../../model/game/unit/unit-path';
import {getMaxAction} from '../../../../model/game/bot/bot-rate';
import {getIsEqualPoint} from '../../../../model/game/point/point-helper';
import {waitForNextFrame} from '../../../../util/time';
import {throwError} from '../../../../util/error';
import {SpringStyleType} from '../client-game-view-container/client-game-view-container';
import {PointType} from '../../../../model/game/point/point-type';
import {squareSize} from '../client-game-view-container/client-game-view-container-const';

type ClientBotPropsType = {
    springApiRef: MutableRefObject<SpringRef<SpringStyleType> | null>;
    springStyleRef: MutableRefObject<SpringValues<SpringStyleType> | null>;
};

export function ClientBot(props: ClientBotPropsType): null {
    const {springApiRef, springStyleRef} = props;
    const {setActivePoint, gameState, animationList, pushAction, localAnimationList, setLocalAnimationList, mapSize} =
        useGameContext();
    const [isReady, setIsReady] = useState<boolean>(false);

    const moveToPoint = useCallback(
        (pointToMove: PointType): Promise<Array<unknown>> => {
            setActivePoint(pointToMove);

            const {current: springApi} = springApiRef;
            const {current: springStyle} = springStyleRef;

            if (!springApi) {
                console.error('springApi is not define');
                return Promise.all<unknown>([]);
            }

            if (!springStyle) {
                console.error('springStyleRef is not define');
                return Promise.all<unknown>([]);
            }

            const currentScale = Number(springStyle.scale.get());

            if (Number.isNaN(currentScale)) {
                console.error('currentScale in NaN', currentScale);
                return Promise.all<unknown>([]);
            }

            const moveResult = springApi.start({
                x: Math.round(((mapSize.width - pointToMove.x * 2 - 1) * squareSize * currentScale) / 2),
                y: Math.round(((mapSize.height - pointToMove.y * 2 - 1) * squareSize * currentScale) / 2),
            });

            return Promise.all<unknown>(moveResult);
        },
        [setActivePoint, springApiRef, springStyleRef, mapSize]
    );

    useEffect(() => {
        waitForNextFrame()
            .then(() => {
                setIsReady(true);
            })
            .catch(throwError);

        console.log('It will be twice!');
    }, []);

    // eslint-disable-next-line complexity, max-statements
    useEffect(() => {
        if (!isReady) {
            return;
        }
        if (localAnimationList.size > 0) {
            console.log('localAnimationList', localAnimationList);
            return;
        }

        if (animationList.length > 0) {
            console.log('animationList', animationList);
            return;
        }

        const {activeGamePlayerId, playerList} = gameState;
        const currentPlayer = getPlayerByPlayerId(activeGamePlayerId, playerList);

        if (!currentPlayer) {
            console.error(`BOT - can not get player by id: ${activeGamePlayerId}`);
            return;
        }

        if (currentPlayer.playerType !== GamePlayerTypeEnum.bot) {
            console.log('currentPlayer.playerType !== GamePlayerTypeEnum.bot');
            return;
        }

        const pointActionList = getPointActionList(gameState);

        if (pointActionList.length === 0) {
            console.log('pointActionList is empty', pointActionList);
            pushAction({
                ...defaultGameAction,
                actionType: GameActionTypeEnum.changeTurn,
            });
            return;
        }

        const movedAction: MovedGameActionType = getMaxAction(pointActionList, gameState);

        moveToPoint(movedAction.moveFrom)
            .then(() => {
                if (movedAction.gameAction.actionType === GameActionTypeEnum.unitMove) {
                    pushAction(movedAction.gameAction);
                    return;
                }

                if (movedAction.gameAction.actionType === GameActionTypeEnum.changeTurn) {
                    pushAction(movedAction.gameAction);
                    return;
                }

                if (getIsEqualPoint(movedAction.moveFrom, movedAction.moveTo)) {
                    pushAction(movedAction.gameAction);
                    return;
                }

                pushAction({
                    actionType: GameActionTypeEnum.unitMove,
                    addUnitList: [],
                    buyUnitType: null,
                    movePointList: getMovePathFromToByPathList(
                        movedAction.moveFrom,
                        movedAction.moveTo,
                        movedAction.movePathList
                    ),
                    toPoint: movedAction.moveTo,
                    unitCurrentPoint: movedAction.moveFrom,
                    unitId: movedAction.gameAction.unitId,
                });

                // TODO: check for player id is first
                console.log('bot turn!');
            })
            .catch(throwError);

        // send action - after that wait for animation is end OR wait for end of action
    }, [moveToPoint, isReady, gameState, pushAction, animationList, localAnimationList, setLocalAnimationList]);

    return null;
}
