const {tr0} = GameLandscapeItemEnum;

// eslint-disable-next-line complexity
export function getMapEditorPlayerIdByColor(color: GamePlayerColorIdEnum): GamePlayerIdEnum {
    switch (color) {
        case GamePlayerColorIdEnum.blue: {
            return GamePlayerIdEnum.player1;
        }
        case GamePlayerColorIdEnum.red: {
            return GamePlayerIdEnum.player2;
        }
        case GamePlayerColorIdEnum.green: {
            return GamePlayerIdEnum.player3;
        }
        case GamePlayerColorIdEnum.black: {
            return GamePlayerIdEnum.player4;
        }
        case GamePlayerColorIdEnum.gray: {
            return GamePlayerIdEnum.playerEmpty;
        }
        default: {
            return GamePlayerIdEnum.playerEmpty;
        }
    }
}

function filterUnitByLandscape(unitList: Array<GameUnitType>, landscape: GameLandscapeType): Array<GameUnitType> {
    const mapSize: SizeType = getLandscapeSize(landscape);

    return unitList.filter<GameUnitType>((unitInList: GameUnitType): unitInList is GameUnitType => {
        return unitInList.x >= 0 && unitInList.y >= 0 && unitInList.x < mapSize.width && unitInList.y < mapSize.height;
    });
}

function filterBuildingByLandscape(
    buildingList: Array<GameBuildingType>,
    landscape: GameLandscapeType
): Array<GameBuildingType> {
    const mapSize: SizeType = getLandscapeSize(landscape);

    return buildingList.filter<GameBuildingType>(
        (buildingInList: GameBuildingType): buildingInList is GameBuildingType => {
            return (
                buildingInList.x >= 0 &&
                buildingInList.y >= 0 &&
                buildingInList.x < mapSize.width &&
                buildingInList.y < mapSize.height
            );
        }
    );
}

function changeListByPoint<LikePointType extends PointType>(
    likePointList: Array<LikePointType>,
    deltaPoint: PointType
): Array<LikePointType> {
    return likePointList.map<LikePointType>((likePoint: LikePointType): LikePointType => {
        return {
            ...likePoint,
            x: likePoint.x + deltaPoint.x,
            y: likePoint.y + deltaPoint.y,
        };
    });
}

export enum ChangeMapSizeEnum {
    bottomMinus = 'bottom-minus',
    bottomPlus = 'bottom-plus',
    leftMinus = 'left-minus',
    leftPlus = 'left-plus',
    rightMinus = 'right-minus',
    rightPlus = 'right-plus',
    topMinus = 'top-minus',
    topPlus = 'top-plus',
}

// eslint-disable-next-line complexity, max-statements
export function changeMapSize(direction: ChangeMapSizeEnum, gameState: GameStateType): GameStateType {
    const {landscape, unitList, buildingList} = gameState;

    switch (direction) {
        case ChangeMapSizeEnum.bottomMinus: {
            const newLandscape: GameLandscapeType = [...landscape];

            newLandscape.pop();

            const newBuildingList: Array<GameBuildingType> = changeListByPoint<GameBuildingType>(buildingList, {
                x: 0,
                y: 0,
            });
            const newUnitList: Array<GameUnitType> = changeListByPoint<GameUnitType>(unitList, {x: 0, y: 0});

            return {
                ...gameState,
                buildingList: filterBuildingByLandscape(newBuildingList, newLandscape),
                landscape: newLandscape,
                unitList: filterUnitByLandscape(newUnitList, newLandscape),
            };
        }
        case ChangeMapSizeEnum.bottomPlus: {
            const newLandscape: GameLandscapeType = [...landscape];

            newLandscape.push([...landscape[landscape.length - 1]]);

            const newBuildingList: Array<GameBuildingType> = changeListByPoint<GameBuildingType>(buildingList, {
                x: 0,
                y: 0,
            });
            const newUnitList: Array<GameUnitType> = changeListByPoint<GameUnitType>(unitList, {x: 0, y: 0});

            return {
                ...gameState,
                buildingList: filterBuildingByLandscape(newBuildingList, newLandscape),
                landscape: newLandscape,
                unitList: filterUnitByLandscape(newUnitList, newLandscape),
            };
        }
        case ChangeMapSizeEnum.topMinus: {
            const newLandscape: GameLandscapeType = [...landscape];

            newLandscape.shift();

            const newBuildingList: Array<GameBuildingType> = changeListByPoint<GameBuildingType>(buildingList, {
                x: 0,
                y: -1,
            });
            const newUnitList: Array<GameUnitType> = changeListByPoint<GameUnitType>(unitList, {x: 0, y: -1});

            return {
                ...gameState,
                buildingList: filterBuildingByLandscape(newBuildingList, newLandscape),
                landscape: newLandscape,
                unitList: filterUnitByLandscape(newUnitList, newLandscape),
            };
        }
        case ChangeMapSizeEnum.topPlus: {
            const newLandscape: GameLandscapeType = [...landscape];

            newLandscape.unshift([...landscape[0]]);

            const newBuildingList: Array<GameBuildingType> = changeListByPoint<GameBuildingType>(buildingList, {
                x: 0,
                y: 1,
            });
            const newUnitList: Array<GameUnitType> = changeListByPoint<GameUnitType>(unitList, {x: 0, y: 1});

            return {
                ...gameState,
                buildingList: filterBuildingByLandscape(newBuildingList, newLandscape),
                landscape: newLandscape,
                unitList: filterUnitByLandscape(newUnitList, newLandscape),
            };
        }
        case ChangeMapSizeEnum.rightMinus: {
            const newLandscape: GameLandscapeType = [...landscape].map<Array<GameLandscapeItemEnum>>(
                (line: Array<GameLandscapeItemEnum>): Array<GameLandscapeItemEnum> => {
                    const newLine: Array<GameLandscapeItemEnum> = [...line];

                    newLine.pop();
                    return newLine;
                }
            );
            const newBuildingList: Array<GameBuildingType> = changeListByPoint<GameBuildingType>(buildingList, {
                x: 0,
                y: 0,
            });
            const newUnitList: Array<GameUnitType> = changeListByPoint<GameUnitType>(unitList, {x: 0, y: 0});

            return {
                ...gameState,
                buildingList: filterBuildingByLandscape(newBuildingList, newLandscape),
                landscape: newLandscape,
                unitList: filterUnitByLandscape(newUnitList, newLandscape),
            };
        }
        case ChangeMapSizeEnum.rightPlus: {
            const newLandscape: GameLandscapeType = [...landscape].map<Array<GameLandscapeItemEnum>>(
                (line: Array<GameLandscapeItemEnum>): Array<GameLandscapeItemEnum> => {
                    return [...line, line[line.length - 1]];
                }
            );

            const newBuildingList: Array<GameBuildingType> = changeListByPoint<GameBuildingType>(buildingList, {
                x: 0,
                y: 0,
            });
            const newUnitList: Array<GameUnitType> = changeListByPoint<GameUnitType>(unitList, {x: 0, y: 0});

            return {
                ...gameState,
                buildingList: filterBuildingByLandscape(newBuildingList, newLandscape),
                landscape: newLandscape,
                unitList: filterUnitByLandscape(newUnitList, newLandscape),
            };
        }
        case ChangeMapSizeEnum.leftMinus: {
            const newLandscape: GameLandscapeType = [...landscape].map<Array<GameLandscapeItemEnum>>(
                (line: Array<GameLandscapeItemEnum>): Array<GameLandscapeItemEnum> => {
                    const newLine: Array<GameLandscapeItemEnum> = [...line];

                    newLine.shift();
                    return newLine;
                }
            );

            const newBuildingList: Array<GameBuildingType> = changeListByPoint<GameBuildingType>(buildingList, {
                x: -1,
                y: 0,
            });
            const newUnitList: Array<GameUnitType> = changeListByPoint<GameUnitType>(unitList, {x: -1, y: 0});

            return {
                ...gameState,
                buildingList: filterBuildingByLandscape(newBuildingList, newLandscape),
                landscape: newLandscape,
                unitList: filterUnitByLandscape(newUnitList, newLandscape),
            };
        }
        case ChangeMapSizeEnum.leftPlus: {
            const newLandscape: GameLandscapeType = [...landscape].map<Array<GameLandscapeItemEnum>>(
                (line: Array<GameLandscapeItemEnum>): Array<GameLandscapeItemEnum> => {
                    return [line[0], ...line];
                }
            );

            const newBuildingList: Array<GameBuildingType> = changeListByPoint<GameBuildingType>(buildingList, {
                x: 1,
                y: 0,
            });
            const newUnitList: Array<GameUnitType> = changeListByPoint<GameUnitType>(unitList, {x: 1, y: 0});

            return {
                ...gameState,
                buildingList: filterBuildingByLandscape(newBuildingList, newLandscape),
                landscape: newLandscape,
                unitList: filterUnitByLandscape(newUnitList, newLandscape),
            };
        }
        default: {
            throw new NeverError(direction);
        }
    }
}

export function getMapEditorDefaultMap(): GameMapType {
    const mapEditorDefaultMap: GameMapType = {
        buildingList: [],
        cemetery: [],
        graveList: [],
        landscape: [
            [tr0, tr0, tr0, tr0, tr0],
            [tr0, tr0, tr0, tr0, tr0],
            [tr0, tr0, tr0, tr0, tr0],
            [tr0, tr0, tr0, tr0, tr0],
            [tr0, tr0, tr0, tr0, tr0],
        ],
        mapId: 'default-map',
        mapName: 'Map name',
        mapType: MapTypeEnum.skirmish,
        triggerList: [],
        unitList: [],
    };

    return mapEditorDefaultMap;
}

export function getMapFromGameState(gameSate: GameStateType, drawPanelState: MapEditorDrawPanelStateType): GameMapType {
    const {landscape, unitList, buildingList} = gameSate;
    const {mapName, mapId} = drawPanelState;

    const map: GameMapType = {
        ...getMapEditorDefaultMap(),
        buildingList,
        landscape,
        mapId,
        mapName: mapName.trim(),
        unitList,
    };

    return map;
}

