import {useEffect} from 'react';

import {useGameContext} from '../../../../provider/game/game-context';
import {getFirstSuitTrigger} from '../../../../model/game/trigger/trigger-helper';

export function ClientTrigger(): null {
    const {gameState} = useGameContext();

    useEffect(() => {
        const trigger = getFirstSuitTrigger(gameState);

        if (trigger) {
            console.log('------ trigger to run !!!!', trigger);
        }
    }, [gameState]);

    return null;
}
