import {useState} from 'react';

import {UiButton} from '../../../../../layout/ui-button/ui-button';
import gamePopupStyle from '../../../../../layout/game-popup/game-popup.scss';
import {usePromptContext} from '../../../../../provider/prompt/prompt-context';
import {GameMapType} from '../../../../../model/map/map-type';
import {deleteUserMap, getUserMapList} from '../../../../../model/map/user-map';
import {GamePopup} from '../../../../../layout/game-popup/game-popup';
import {Locale, useLocale} from '../../../../../provider/locale/locale-context';
import {sortCompare} from '../../../../../util/string';
import {noop} from '../../../../../util/function';
import {GameSnackbar} from '../../../../../layout/game-snackbar/game-snackbar';

import clientLoadGamePopupStyle from './user-map-manager-popup.scss';

export type UserMapManagerPopupPropsType = {
    handleClose: () => void;
    isUserMapManagerPopupOpen: boolean;
    loadMap: (mapToLoad: GameMapType) => void;
};

export function UserMapManagerPopup(props: UserMapManagerPopupPropsType) {
    const {localeName} = useLocale();
    const {isUserMapManagerPopupOpen, handleClose, loadMap} = props;
    const {prompt} = usePromptContext();
    const userMapList: Array<GameMapType> = getUserMapList();
    const [isMapLoadedSnackbarOpen, setIsMapLoadedSnackbarOpen] = useState<boolean>(false);
    const [isMapRemovedSnackbarOpen, setIsMapRemovedSnackbarOpen] = useState<boolean>(false);

    return (
        <>
            <GamePopup isOpen={isUserMapManagerPopupOpen}>
                <h3 className={gamePopupStyle.game_popup__header}>
                    <Locale stringKey="MAP_EDITOR__MAPS" />
                </h3>

                <div className={clientLoadGamePopupStyle.client_load_game_popup__saved_game_list}>
                    {userMapList.length === 0 ? (
                        <p className={clientLoadGamePopupStyle.client_load_game_popup__no_saved_game_text}>
                            <Locale stringKey="MAP_EDITOR__NO_SAVED_MAPS" />
                        </p>
                    ) : null}

                    {[...userMapList]
                        .sort((mapA: GameMapType, mapB: GameMapType): number => {
                            return sortCompare(localeName, mapA.mapName, mapB.mapName);
                        })
                        .map<JSX.Element>((userMap: GameMapType): JSX.Element => {
                            return (
                                <div
                                    className={clientLoadGamePopupStyle.client_load_game_popup__saved_game_item}
                                    key={userMap.mapId}
                                >
                                    <p
                                        className={
                                            clientLoadGamePopupStyle.client_load_game_popup__saved_game_item__name
                                        }
                                    >
                                        {userMap.mapName}
                                    </p>
                                    <div
                                        className={
                                            // eslint-disable-next-line max-len
                                            clientLoadGamePopupStyle.client_load_game_popup__saved_game_item__button_list
                                        }
                                    >
                                        <UiButton
                                            langKey="UI__DELETE"
                                            onClick={() => {
                                                prompt({
                                                    header: 'MAP_EDITOR__YOU_ARE_SURE_TO_DELETE_MAP',
                                                    index: 0,
                                                    onNo: noop,
                                                    onYes: () => {
                                                        deleteUserMap(userMap.mapId);
                                                        setIsMapRemovedSnackbarOpen(true);
                                                    },
                                                });
                                                handleClose();
                                            }}
                                        />
                                        <UiButton
                                            langKey="UI__LOAD"
                                            onClick={() => {
                                                prompt({
                                                    header: 'MAP_EDITOR__UNSAVED_MAP_PROGRESS_WILL_BE_LOST',
                                                    index: 0,
                                                    onNo: noop,
                                                    onYes: () => {
                                                        loadMap(userMap);
                                                        setIsMapLoadedSnackbarOpen(true);
                                                    },
                                                });
                                                handleClose();
                                            }}
                                        />
                                    </div>
                                </div>
                            );
                        })}
                </div>

                <div className={gamePopupStyle.game_popup__footer}>
                    <UiButton langKey="UI__CLOSE" onClick={handleClose} />
                </div>
            </GamePopup>

            <GameSnackbar isOpen={isMapLoadedSnackbarOpen} setClose={setIsMapLoadedSnackbarOpen}>
                <Locale stringKey="MAP_EDITOR__MAP_WAS_LOADED" />
            </GameSnackbar>
            <GameSnackbar isOpen={isMapRemovedSnackbarOpen} setClose={setIsMapRemovedSnackbarOpen}>
                <Locale stringKey="MAP_EDITOR__MAP_WAS_REMOVED" />
            </GameSnackbar>
        </>
    );
}
