import {useGameContext} from '../../../../provider/game/game-context';
import {GameActionTypeEnum} from '../../../../provider/game/game-context-type';
import {availableToBuyUnitTypeList} from '../../../../model/game/unit/unit-const';
import {PageHeader} from '../../../../layout/page/page-header/page-header';
import {Page} from '../../../../layout/page/page';
import {PageContent} from '../../../../layout/page/page-content/page-content';
import {PageFooter} from '../../../../layout/page/page-footer/page-footer';

import {UnitStoreItem} from './unit-store-item/unit-store-item';
import {getUnitPriceList} from './unit-store-helper';
import {UnitPriceItemType} from './unit-store-type';
import unitStoreStyle from './unit-store.scss';

export function UnitStore(): JSX.Element | null {
    const {pushAction, gameState, isOpenStore} = useGameContext();
    const {cemetery, activeGamePlayerId} = gameState;

    if (!isOpenStore) {
        return null;
    }

    return (
        <Page className={unitStoreStyle.unit_store}>
            <PageHeader
                langKey="UNIT_STORE__HEADER"
                onBackButtonClick={async () => {
                    await pushAction({
                        actionType: GameActionTypeEnum.closeStore,
                        addUnitList: [],
                        buyUnitType: null,
                        movePointList: [],
                        toPoint: {x: -1, y: -1},
                        unitCurrentPoint: {x: -1, y: -1},
                        unitId: '',
                    });
                }}
            />
            <PageContent
                className={unitStoreStyle.unit_store__unit_list}
                contentClassName={unitStoreStyle.unit_store__unit_list__content}
                hasBorder
            >
                {getUnitPriceList(activeGamePlayerId, availableToBuyUnitTypeList, cemetery).map(
                    (priceItem: UnitPriceItemType): JSX.Element => {
                        const {unitType, cost, unitId} = priceItem;

                        return <UnitStoreItem cost={cost} key={unitType} unitId={unitId} unitType={unitType} />;
                    }
                )}
            </PageContent>
            <PageFooter className={unitStoreStyle.unit_store__footer} />
        </Page>
    );
}
