import {ReactNode} from 'react';
import {useLocation} from 'react-router-dom';

import {useGoogleAnalytics} from '../../hook/google-analytics/google-analytics';
import {googleAnalyticsId} from '../../const';
import {classNames} from '../../util/css';

import pageStyle from './page.scss';

type PagePropsType = {
    children: ReactNode;
    className?: string;
};

export function Page(props: PagePropsType): JSX.Element {
    const {children, className} = props;
    const location = useLocation();
    const {pathname} = location;

    useGoogleAnalytics({googleAnalyticsId, pathname});

    return <div className={classNames(pageStyle.page, className)}>{children}</div>;
}
