import {useEffect, useRef, useState} from 'react';

import {useGameContext} from '../../../../provider/game/game-context';
import {GameSnackbar} from '../../../../layout/game-snackbar/game-snackbar';
import {GamePlayerType} from '../../../../model/game/player/player-type';
import {getPlayerByPlayerId} from '../../../../model/game/player/player-helper';
import {Locale} from '../../../../provider/locale/locale-context';
import {LangKeyType} from '../../../../provider/locale/translation/type';

import {defeatPlayerColorLangKey} from './client-game-player-is-defeat-const';

export function ClientGamePlayerIsDefeat(): JSX.Element {
    const [isUserDefeatSnackbarOpen, setIsUserDefeatSnackbarOpen] = useState<boolean>(false);
    const [defeatLangKey, setDefeatLangKey] = useState<LangKeyType>('UI__EMPTY_STRING');
    const {gameState} = useGameContext();
    const {playerList} = gameState;
    const prevPlayerListRef = useRef<Array<GamePlayerType>>([...playerList]);

    useEffect(() => {
        if (prevPlayerListRef.current.length <= playerList.length) {
            return;
        }

        const defeatedPlayer: GamePlayerType | void = prevPlayerListRef.current.find<GamePlayerType>(
            (prevPlayerInList: GamePlayerType): prevPlayerInList is GamePlayerType => {
                return !getPlayerByPlayerId(prevPlayerInList.gamePlayerId, playerList);
            }
        );

        prevPlayerListRef.current = [...playerList];

        if (!defeatedPlayer) {
            console.error('can not find defeated player');
            return;
        }

        setDefeatLangKey(defeatPlayerColorLangKey[defeatedPlayer.gamePlayerColorId]);
        setIsUserDefeatSnackbarOpen(true);
    }, [playerList]);

    return (
        <GameSnackbar isOpen={isUserDefeatSnackbarOpen} setClose={setIsUserDefeatSnackbarOpen}>
            <Locale stringKey={defeatLangKey} />
        </GameSnackbar>
    );
}
