import {ReactNode} from 'react';

import {classNames} from '../../../util/css';

import pageFooterStyle from './page-footer.scss';

type PageFooterPropsType = {
    children?: ReactNode;
    className?: string;
};
export function PageFooter(props: PageFooterPropsType): JSX.Element {
    const {children, className} = props;

    return <div className={classNames(pageFooterStyle.page_footer, className)}>{children}</div>;
}
