/* global IS_PRODUCTION */

import {BrowserRouter, Route, Routes, HashRouter} from 'react-router-dom';

import {ClientHome} from '../page/client/client-home/client-home';
import {Error404} from '../page/service/error-404/error-404';
import {Page} from '../layout/page/page';
import {ClientLocalGameSetup} from '../page/client/client-local-game-setup/client-local-game-setup';
import {ClientGame} from '../page/client/client-game/client-game';
import {ClientPlay} from '../page/client/client-play/client-play';
import {ClientSkirmish} from '../page/client/client-skirmish/client-skirmish';
import {MapEditor} from '../page/client/map-editor/map-editor';
import {ClientSkirmishUserMap} from '../page/client/client-skirmish/client-skirmish-user-map';
import {ClientSettings} from '../page/client/client-settings/client-settings';
import {ClientInstructions} from '../page/client/client-instructions/client-instructions';
import {ClientAbout} from '../page/client/client-about/client-about';

import {appRoute} from './app-route';

export function AppRouting(): JSX.Element {
    const switchNode = (
        <Routes>
            <Route Component={ClientHome} path={appRoute.root.path} />
            <Route Component={ClientPlay} path={appRoute.play.path} />
            <Route Component={ClientSkirmish} path={appRoute.skirmish.path} />
            <Route Component={ClientSkirmishUserMap} path={appRoute.skirmishUserMaps.path} />
            <Route Component={ClientLocalGameSetup} path={appRoute.setupMap.path} />
            <Route Component={ClientGame} path={appRoute.clientGame.path} />
            <Route Component={MapEditor} path={appRoute.mapEditor.path} />

            <Route Component={ClientSettings} path={appRoute.settings.path} />
            <Route Component={ClientInstructions} path={appRoute.instructions.path} />
            <Route Component={ClientAbout} path={appRoute.about.path} />

            <Route
                element={
                    <Page>
                        <Error404 />
                    </Page>
                }
                path="*"
            />
        </Routes>
    );

    if (IS_PRODUCTION) {
        return <HashRouter>{switchNode}</HashRouter>;
    }

    return <BrowserRouter>{switchNode}</BrowserRouter>;
}
