import {ReactNode} from 'react';

import {classNames} from '../../../util/css';

import pageContentStyle from './page-content.scss';

type PageContentPropsType = {
    children: ReactNode;
    className?: string;
    contentClassName?: string;
    hasBorder?: boolean;
};

export function PageContent(props: PageContentPropsType): JSX.Element {
    const {children, hasBorder, className, contentClassName} = props;

    return (
        <div className={classNames(pageContentStyle.page_content, className)}>
            <div className={classNames(hasBorder ? pageContentStyle.page_content__border : '', contentClassName)}>
                {children}
            </div>
        </div>
    );
}
